.Modal {
    position: fixed;
    z-index: 500;
    background-color: white;
    width: 70%;
    border: 1px solid #ccc;
    box-shadow: 1px 1px 1px black;
    padding: 16px;
    left: 15%;
    top: 30%;
    box-sizing: border-box;
    transform: translateY(-100vh);
    opacity: 0;
    transition: all 0.3s ease-out;
}

.Show {
    transform: translateY(0) !important;
    opacity: 1 !important;
}



@media (min-width: 600px) {
    .Modal {
        width: 500px;
        left: calc(50% - 250px);
    }
}