.NavigationItems {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}
.NavigationItems p{
  margin: 0;
  width: 100%;
  height: 100%;
}

@media (min-width: 500px){
  .NavigationItems {
    flex-direction: row;
  }
}
